.error {
    @include flexCenter;

    &__icon {
        font-size: 132px;
        color: #d4d4d4;
    }

    &__inner {
        margin: auto;
        text-align: center;
    } //error__title

    &__title {
        margin-bottom: 30px;
        font-family: $f-bold;
        font-size: 100px;
        color: $gray--light;

        .fa {
            font-size: 110px;
        }
    }

    &__text {
        line-height: 1.8;
    }

    &__link {
        font-size: 16px;
    }
} //error
