.rules {
    padding-bottom: 50px;

    .h1 {
        margin: $gutter * 2 0;
    }

    .h3 {
        margin-bottom: $gutter;
        margin-top: $gutter * 2;
    }

    p {
        margin-bottom: $gutter;
    }

    ul {
        margin: $gutter 0;

        li {
            margin-bottom: 10px;
            @include listIcon;

            .h5 {
                margin-bottom: 0;
            }
        }

        ul {
            margin-left: 15px;

            li {

                &::before {
                    width: 5px;
                    height: 5px;
                    border-radius: 50%;
                }
            }
        }
    }

    .h5 {
        font-family: $f-medium;
        font-size: 16px;
        font-weight: 500;
    }
} //rules
