.blog {
    padding: 30px 0;
    background-color: #fff;

    &__wrap {
        @include flex;
        @include respond-to(xs) {
            flex-wrap: wrap;
        }
    } //blog__wrap

    &__content {
        margin-right: 30px;
        flex: 1;
        @include respond-to(xs) {
            margin-right: 0;
        }
    } //blog__content

    &__sidebar {
        width: 300px;
        @include respond-to(xs) {
            width: 100%;
        }
    } //blog__sidebar

    &__title {
        margin: 25px 0;
        color: $color-main;
    } //blog__title

    &__subtitle {
        margin: $gutter 0;
        font-family: $f;
        font-weight: 400;
    } //blog__subtitle

    &__text {
        margin-bottom: $gutter;
    } //blog__text

    &__img {
        padding: 5px 5px 0;
        background-color: $color-bg--lighten;

        span {
            display: block;
            padding: 10px 0 11px;
            @extend %textLight;
            font-size: 13px;
            text-align: center;
        }
    } //blog__img

    &__list {
        margin-bottom: $gutter;

        li {
            padding-top: 2px;
            padding-bottom: 6px;
            @include listIcon;
        }
    } //blog__list
} //blog

.blog-item {

    &__box {
        @include flexCenter;
    } //blog-item__box

    &__info {
        padding-right: 20px;
        @extend %textLight;
        font-size: 13px;
        position: relative;

        &::after {
            content: attr(data-counter);
            position: absolute;
            top: 0;
            right: 0;
        }

        & + & {
            margin-left: 15px;
        }

        .fa {
            margin-right: 5px;
        }
    } //blog-item__info
} //blog-item

.widget {
    margin-bottom: $gutter * 2;

    &:last-child {
        margin-bottom: 0;
    }

    &__title {
        display: block;
        padding: 10px 0;
        margin-bottom: $gutter;
        color: $gray--darkest;
        border-bottom: 1px solid $color-border--light;

        span {
            text-transform: uppercase;
            font-family: $f-bold;
        }
    } //widget__title

    &__inner {
        @include flex;
        align-items: flex-start;
        flex-wrap: wrap;
        @include respond-to(xs) {
            justify-content: flex-start;
        }
    } //widget__inner

    &__item {
        overflow: hidden;
    } //widget__item

    &__btn {
        display: inline-block;
        width: auto;
        margin: 15px 5px 0;
        text-transform: initial;
    } //widget__btn

    &--instagram {

        .widget {

            &__item {
                width: 140px;
                height: 140px;
                margin: 5px;
                @include respond-to(xs) {
                    width: 192px;
                    height: 192px;
                }
                @include max-screen(414) {
                    width: 187px;
                    height: 187px;
                }
                @include max-screen(375) {
                    width: 167px;
                    height: 167px;
                }
                @include respond-to(i5) {
                    width: 140px;
                    height: 140px;
                }
            }
        }
    } //widget--instagram

    &__post {
        width: 100%;
        margin: auto auto $gutter * 2;

        &:last-child {
            margin-bottom: 0;
        }
        @include respond-to(xs) {
            width: 47%;
            margin-right: 20px;
            margin-left: 0;

            &:nth-child(2n) {
                margin-right: 0;
            }
        }
        @include respond-to(i5) {
            width: 100%;
            margin-right: 0;
        }
    } //widget--posts

    .button-box {
        @include flexCenter;
        flex-wrap: wrap;
    }

    .button-social--instagram {
        margin-left: auto;
        padding: 2px 25px 4px;
        @include respond-to(xs) {
            margin-left: 20px;
        }
        @include respond-to(i5) {
            margin-left: 15px;
        }
    }
} //widget

.post {

    &:hover {

        .post__title {

            span {
                background-image: linear-gradient(
                    180deg,
                    $color-third,
                    $color-third
                );
                background-repeat: no-repeat;
                background-position: 4px bottom;
                background-size: 100% 10px;
            }
        }
    }

    &__img {
        position: relative;

        img {
            @include coverdiv;
        }

        .spacer {
            padding-top: 100%;
            background-color: $gray--lighten;
        }
    } //post__img

    &__subtitle {
        display: block;
        margin: 13px 0 8px;
        font-family: $f;
        font-size: 12px;
        letter-spacing: 1.5px;
        color: $gray--light;
        text-transform: uppercase;
    } //post__subtitle

    &__title {
        margin: 0;
        font-family: $f-medium;
        color: $gray--darkest;
    } //post__title
}
