.page-auth {

    .footer {
        background-color: $color-bg--lighten;
    }
} //.page-auth

.auth {
    padding-top: 40px;

    @include respond-to(xs) {
        padding: 30px 0;
    }

    .form {
        padding: 0;
        // position: static;

        &.is-success--phone {
            min-height: 450px;
        }

        @include respond-to(xs) {
            max-width: 100%;
            width: 100%;
        }

        &-success {
            padding: 0;
            text-align: center;
            background-color: $color-bg--lighten;

            &__inner {
                margin: 0;
            }
        }

        .form-success {

            .form {

                &__header {
                    max-width: 375px;
                }
            }
        }
    }

    .page-title {
        margin: auto;
        font-family: $f-bold;
        font-weight: 600;
        font-size: 23px;

        &__wrap {
            margin-bottom: $gutter * 1.5;
        }

        span {
            font-family: $f;
            font-size: 25px;
            color: $color-main;
        }
    }

    .tab {

        &__wrap {
            max-width: 375px;
            width: 375px;
            margin: auto;
            @include max-screen(375) {
                max-width: 355px;
                width: 355px;
            }
            @include respond-to(i5) {
                max-width: 300px;
                width: 300px;
            }
        }

        &__titles {
            margin-bottom: $gutter;
            justify-content: center;
        }

        &__content {
            padding: 0;
        }
    }

    .phone-confirm__input {
        background-color: transparent;
    }
} //.auth
