.contacts {
    padding-bottom: 50px;

    @include respond-to(xs) {
        padding-top: 10px;
    }

    &__wrap {
        @extend %box;
        @include flex;
        @include respond-to(sm) {
            box-shadow: none;
            background-color: $color-bg--lighten;
            flex-direction: column;
        }
    } //contacts__wrap

    &__block {
        width: 50%;
        position: relative;
        @include respond-to(xs) {
            width: 100%;
            display: flex;
            justify-content: center;
        }

        &--left {
            color: #fff;
            background-color: #2f2f2f;
            @include respond-to(xs) {
                margin-bottom: 15px;
            }
        }

        &--right {
            padding-bottom: 56.25%;
            background-color: #2f2f2f;
        }
    } //contacts__block

    &__inner {
        max-width: 375px;
        margin: auto;
        padding: 40px 0;
        @include respond-to(xs) {
            padding: 15px 15px 30px;
            margin: 0;
        }
    } //contacts__inner

    &__map {
        @include coverdiv;
    } //contacts__map

    &__form {
        position: relative;
        overflow: hidden;

        .success {
            text-align: center;
        }
    } //contacts__form

    &__textarea {
        width: 100%;
        min-height: 100px;
        max-height: 170px;
        padding: 6px 15px;
        color: $gray;
        border-radius: $border-radius;
        resize: none;
        @include bb-scroll;
    } //contacts__textarea

    &__btn {
        width: 100%;
        text-transform: uppercase;
    } //contacts__btn

    .buttons-box {
        margin-top: 15px;
    } //contacts .button-box

    .page-title {
        margin-bottom: 15px;
        font-family: $f-light;
        text-align: center;
        text-transform: uppercase;
        color: $gray--lightest;

        &__wrap {
            margin-bottom: 30px;
            font-family: $f-light;
            flex-direction: column;
        }

        &__small {
            max-width: 375px;
            text-align: center;
            color: $gray--light;
        }

        span {
            color: $gray--lightest;
        }
    } //contacts .page-title

    .bb-input {
        display: block;
        margin-bottom: 20px;

        &__input {
            width: 100%;
        }
    } //contacts .bb-input
} //contacts

.item-icon {
    width: 100%;
    margin-bottom: 20px;
    font-family: $f-light;
    font-size: 14px;
    color: $gray--lighten;
    @include flexCenter;

    &__wrap {
        margin-bottom: 15px;
        @include flexCenter;
        flex-wrap: wrap;
    } //item-icon__wrap

    &__icon {
        margin-right: 15px;
        width: 23px;
        height: 23px;
        fill: $color-main;
    } //item-icon__icon

    &__text {
        flex: 1;
    } //item-icon__text
} //item-icon
