.page-promo {

    .header {
        @include min-screen(481) {
            // border-bottom: 1px solid rgba(#fff, 0.4);
            // background-color: transparent;
            // position: absolute;
            // top: 0;
            // left: 0;
            // width: 100%;
            // z-index: 9;
        }

        &__inner {
            height: 50px;
            border-bottom: none;
        }
    } //page-promo .header

    .nav {
        margin-left: auto;

        @include respond-to(xs) {
            top: 49px;
            background-color: rgba(#000, 0.8);
        }

        &__item {

            &.is-active {

                .nav__link {
                    color: $color-main;
                }
            }
        }

        &__link {
            line-height: 50px;
            // color: #fff;

            // &::after {
            //     background-color: $color-main;
            // }
        }

        &-dropdown {
            top: 50px;
        }
    } //page-promo .nav

    .firstscreen {
        min-height: 600px;
        padding-top: 50px;
        margin-bottom: 0;
        @include flexCenter;
        justify-content: center;
        @include cover;
        @include sectionOverlay;

        &__content {
            margin: auto;
            text-align: center;
            @include respond-to(xs) {
                padding: 0 10px;
            }
        }

        &__title {
            margin-top: 0;
            margin-bottom: 30px;
            text-align: center;
            text-transform: uppercase;
            font-family: roboto-400c;
            font-size: 42px;
            letter-spacing: 1px;
        }

        &__subtitle {
            font-family: $f-light;
            font-size: 20px;
        }

        &__buttons-box {
            margin-top: 50px;
        }
    } //page-promo .firstscreen

    .color-primary {
        font-family: $f-bold;
    } //page-promo .color-primary
} //page-promo

.promo {
    padding-bottom: 0;

    &__firstscreen {
        min-height: 600px;
        @include sectionOverlay;
        @include cover;

        @include respond-to(xs) {
            min-height: 200px;
            margin-bottom: $gutter;
            display: flex;
            flex-direction: column;

            & > .container {
                margin: auto;
            }
        }
    } //.promo__firstscreen

    &__wrap {
        padding-top: 0;
        position: relative;

        .firstscreen {

            &::before {
                background-color: rgba(#000, 0.6);
            }

            @include respond-to(xs) {
                min-height: auto;
            }
        }
    } //.promo__wrap

    &__in {
        padding-top: 65px;
        @include flexCenter;
        justify-content: space-between;

        @include respond-to(xs) {
            padding-top: 0;
            flex-direction: column;
            align-items: flex-start;
        }
    } //.promo__in

    &__content {
        color: #fff;
        margin-right: auto;
        flex: 1;

        @include respond-to(xs) {
            margin-right: 0;
            text-align: center;
        }
    } //.promo__content

    &__subtitle {
        font-family: $f;
        font-size: 18px;
        letter-spacing: 0.5px;
    } //.promo__subtitle

    &__title {

        span {
            color: $color-main;
        }

        &--big {
            text-transform: uppercase;
        } //.promo__title--big

        &--i {
            font-family: $f-bold-i;
        } //.promo__title--i
    } //.promo__title

    &__form {
        background-color: rgba(#fff, 0.75);
        border-radius: $border-radius;

        @include min-screen(481) {

            .bb-input {

                &__input {
                    border-color: #909090;
                }
            }

            .bb-checkbox {

                &__box {
                    border-color: $color-border;
                }

                &__title {
                    color: #000;
                }
            }

            .bb-title--devider {
                color: #000;

                &::before,
                &::after {
                    background-color: #909090;
                }
            }
        }

        @include respond-to(xs) {
            max-width: 100%;
            width: auto;
            margin: 0 10px;
            padding: 20px 0 0;

            .bb-input {

                &__input {
                    height: 45px;
                    border-color: #909090;
                }
            }
        }

        .form {

            &__header {
                // text-align: left;
            } //.promo__form .form__header

            &__title {
                font-family: $f-bold;
                font-size: 25px;
                line-height: 1;
                letter-spacing: 0;
                // text-align: left;

                @include respond-to(xs) {
                    display: block;
                    width: 100%;
                    font-size: 29px;
                    line-height: 1.2;
                    text-align: center;

                    // br {
                    //     display: none;
                    // }
                }
            }
        }

        .form-success {
            background-color: #fff;
        }
    } //.promo__form

    .bb-section {
        @include respond-to(xs) {
            margin-bottom: 0;
        }
    } //.promo .bb-section

    .bb-best {

        &__inner {
            @include min-screen(481) {
                @include row-flex;
            }
        }

        .catalog-item--small {
            @include min-screen(481) {
                @include col;
                @include size(4);
            }
            @include respond-to(xs) {
                margin-bottom: 15px;
            }
        }
    } //.promo .bb-best

    .bb-about {
        padding-bottom: 30px;
    } //.promo .bb-about

    .bb-title {

        &:not(.bb-title--devider) {
            margin-bottom: 30px;
        }

        &__title {
            @include respond-to(xs) {
                font-family: $f-bold;
                color: #000;
            }
        }
    } //.promo .bb-title

    .bb-slider--promo {

        .slick-slide {
            margin: 3px;
        } //.promo .bb-slider--promo.slick-slide

        .slick-dots {
            @include respond-to(xs) {
                bottom: -25px;
            }
        }
    } //.promo .bb-slider--promo

    .bb-whywe {
        @include respond-to(xs) {
            padding: 40px 0 50px;
        }

        .bb-title__title {
            color: #fff;
        }
    } //.promo .bb-whywe
} //.promo

.bb-clients {
    padding-top: 0;

    &__items {
        @include flexCenter;
        margin-top: 30px;
        @include respond-to(sm) {
            margin-top: 0;
            flex-wrap: wrap;
        }
    } //bb-clients__items

    &__item {
        margin: auto;
        @include respond-to(sm) {
            flex-basis: 30%;

            img {
                margin: auto auto 30px;
                width: 55%;
            }
        }
        @include respond-to(xs) {

            img {
                width: 90%;
                margin: 0;
            }
        }
    } //bb-clients__item

    &__icon {
        font-size: 32px;
    } //bb-clients__icon
} //bb-clients

.bb-whywe {
    @include cover;
    @include sectionOverlay;

    &__buttons-box {
        margin-top: 50px;
    }
} //bb-whywe
