.page-order {

    .header {

        &__logo {
            @include vx;
        }
    }
} //.page-order

.order {
    padding-bottom: 50px;

    &__top-line {
        height: 65px;
        line-height: 65px;
        border-bottom: 1px solid $color-border--lightest;
    } //.order__top-line

    &__inner {
        @include flex;
    } //.order__inner

    &__left {
        width: calc(100% - 375px - 30px);
    } //.order__left

    &__right {
        width: 375px;
        margin-left: 30px;
        position: relative;
        top: -55px;
    } //.order__right

    .card-services-item {
        border: none;
        position: relative;

        &:hover {
            background-color: #fff;

            .card-services-item {

                &__btn {
                    display: inline-flex;
                }
            }
        }

        &.is-booked {

            &:hover {

                .card-services-item__action_del {
                    display: inline-flex;
                }
            }
        }

        &__left {
            padding-left: 0;
        }

        &__action {
            display: block;

            &_del {
                display: inline-flex;
                align-items: center;

                .card-services-item__text {
                    color: #000;
                }
            }
        }

        &__btn {
            display: none;

            &::before {
                position: relative;
                top: 1px;
            }
        }
    }
} //.order

.order-info {
    @extend %box;

    &__title {
        padding: 15px 20px;
        font-family: $f-medium;
        color: #000;
        border-bottom: 1px solid $color-border--lightest;
    } //.order-info__title

    &__footer {
        padding: 15px 20px;
        border-top: 1px solid $color-border--lightest;
        @include flexCenter;

        .card-services-item__text {
            margin-left: auto;
        }
    } //.order-info__footer

    &__info {
        @extend %textLight;
        font-style: italic;
        font-size: 13px;
        color: $gray--light;
    }
} //.order-info
