.page-catalog {

    .page-title {
        @include respond-to(xs) {
            margin: 0 auto 15px;
            font-family: $f-medium;
            font-weight: 500;
            font-size: 32px;
            text-align: center;
            @include textShadow;

            span {
                display: block;
                font-family: $f-medium;
                font-weight: 500;
                font-size: 32px;
            }
        }

        &__wrap {
            max-width: 1200px;
            margin: 0 auto 30px;

            @include respond-min(xs) {
                background-image: none !important;
            }

            @include respond-to(lg) {
                padding: 0 10px;
                margin: 0 auto 30px;
            }

            @include respond-to(xs) {
                min-height: 180px;
                margin: 0 auto 15px;
                padding: 40px 0 45px;
                @include cover;
                position: relative;
                flex-direction: column;

                &::before {
                    display: block;
                    content: '';
                    background-color: rgba(#000, 0.45);
                    @include tr;
                    @include coverdiv;
                }

                & > * {
                    z-index: z(default);
                    color: #fff;
                }

                span,
                strong {
                    color: #fff;
                }
            }
        }
    }

    .page-subtitle {
        @include respond-to(xs) {
            font-family: $f-light;
            font-size: 18px;
            @include textShadow;

            strong {
                font-size: 20px;
            }
        }
    }
} //.page-catalog

.catalog {
    padding-bottom: 50px;

    &__inner {
        margin-bottom: $section-indent;
        @include flex;
        align-items: flex-start;
        @include respond-to(xs) {
            flex-wrap: wrap;
            flex-direction: column;
        }
    } //.catalog__inner

    &__title {
        font-size: 28px;
        text-align: center;

        @include respond-to(xs) {
            margin: 0 0 20px;
        }
    } //.catalog__title

    &__top-line {
        margin-left: auto;
        @include flexCenter;
        @include respond-to(xs) {
            margin: 0 auto;
        }
    } //.catalog__top-line

    &__view-toggle {
        margin-left: 30px;
        @include respond-to(xs) {
            margin: 0 0 15px auto;
            width: 100%;

            .view-toggle__item {
                width: 50%;
            }
        }
    } //.catalog__view-toggle

    &__seoblock {
        margin-left: 280px;
    } //.catalog__seoblock

    &__promo-block {
        margin-bottom: 15px;
    } //.catalog__promo-block

    &__pagination {
        margin-top: $gutter * 2.5;
        margin-left: 280px;

        @include respond-to(sm) {
            margin-left: 0;
        }
    } //.catalog__pagination
} //.catalog

.catalog-filter {
    width: 250px;
    margin-right: 30px;

    @include respond-to(sm) {
        margin-right: 0;
        position: fixed;
        top: 0;
        left: 0;
        width: 100%;
        bottom: 0;
        z-index: 300;
        background-color: #fff;
        transform: translateX(-100vw);
        transition: $trs;
        will-change: transform;

        &.is-open {
            transform: translateX(0);
        }
    }

    &__toggle {
        margin-bottom: 15px;
        @include respond-to(xs) {
            height: 60px;
            margin: 0;
            border: 1px solid transparent;
            @include tr;
        }

        .bb-checkbox--radio_inline {
            position: relative;
            @include respond-to(xs) {
                width: 44px;
                height: 44px;
                padding: 0;

                &:first-child {
                    padding-left: 0;
                }

                &:last-child {
                    padding-left: 0;
                }

                .bb-checkbox__title {
                    display: none;
                }
            }

            &::before {
                @include fa('solid');
                font-size: 16px;
                position: absolute;
                top: 5px;
                left: 25px;
                @include respond-to(xs) {
                    left: 50%;
                    top: 50%;
                    transform: translate(-50%, -50%);
                }
            }

            &:first-child {
                padding-left: 20px;

                &::before {
                    content: '\f0c9';
                }
            }

            &:last-child {
                padding-left: 5px;

                &::before {
                    content: '\f041';
                }
            }
        }
    } //.catalog-filter__toggle

    &__header {
        display: flex;
        align-items: center;
        height: 50px;
        padding: 0 15px;
        border-bottom: 1px solid $color-border--lightest;

        @include respond-to(sm) {
            height: 50px;
            position: fixed;
            top: 0;
            left: 0;
            width: 100%;
        }

        .catalog-filter {

            &__title {
                flex: 1;
                width: auto;
                margin: 0;
                font-family: $f;
                font-size: 18px;
                line-height: 1.2;
                position: relative;
                @include respond-to(sm) {
                    display: flex;
                    flex-direction: column;
                    justify-content: center;
                }

                span {
                    display: none;
                    font-family: $f-light;
                    font-size: 12px;
                    color: $color-text;
                    @include respond-to(sm) {
                        display: inline-block;
                    }
                }
            }
        }

        .button-link {
            margin-left: auto;
            font-family: $f-light;
            font-size: 13px;
            @include respond-to(sm) {
                padding: 0 3px;

                span {
                    display: none;
                }
            }

            .catalog-filter__btn_close {
                margin-right: 0;
                @include min-screen(769) {
                    display: none;
                }
            }
        }
    } //.catalog-filter__header

    &__body {
        @include respond-to(sm) {
            position: fixed;
            top: 50px;
            left: 0;
            width: 100%;
            height: calc(100% - 90px);
            bottom: 40px;
            overflow-y: auto;
        }
    } //.catalog-filter__body

    &__footer {
        display: none;
        @include respond-to(sm) {
            background-color: #fff;
            display: flex;
            align-items: center;
            justify-content: center;
            position: fixed;
            bottom: 0;
            left: 0;
            width: 100%;
            height: 40px;
        }
    } //.catalog-filter__footer

    &__inner {
        @extend %box;
        @include respond-to(sm) {
            box-shadow: none;
        }
    } //.catalog-filter__inner

    &__item {
        padding: 15px;
        border-bottom: 1px solid $color-border--lightest;
        display: flex;
        flex-wrap: wrap;

        & + .catalog-filter__item {
            margin-top: 0;
        }

        @include respond-to(sm) {
            padding: 0;
            margin: 0;
            box-shadow: none;
        }

        &:last-child {
            border-bottom: none;
        }

        &_head {
            @include flexCenter;

            .catalog-filter {

                &__title {
                    width: auto;
                    margin: 0;
                    font-family: $f;
                    font-size: 18px;
                }
            }

            .button-link {
                margin-left: auto;
                font-family: $f-light;
                font-size: 13px;
            }
        } //.catalog-filter__item_head

        &_service {

            .bb-checkbox {
                margin: auto auto 5px;
            } //.catalog-filter__item_service .bb-checkbox
        } //.catalog-filter__item_service

        &_place,
        &_gender {

            .bb-checkbox {
                width: 100%;
                margin-bottom: 5px;
            }
        } //.catalog-filter__item_place

        &_metro,
        &_area {
            padding-bottom: 20px;
        } //.catalog-filter__item_area

        &_buttons {

            .button-animate {
                padding: 8px 45px;
                border: 1px solid $color-border;
            }
        } //.catalog-filter__item_buttons

        .bb-checkbox {
            @include text-overflow;

            @include respond-to(sm) {
                display: inline-block;
                width: auto;
                padding: 5px 0;
                margin: 0 10px 6px 0;
            }
        }
    } //.catalog-filter__item

    &__title {
        width: 100%;
        margin: 0 0 15px;
        font-family: $f-bold;
        font-size: 15px;
        color: $color-text--darkest;

        @include respond-to(xs) {
            font-size: 16px;
        }
    } //.catalog-filter__title

    &__content {
        width: 100%;
        @include flexColumn;

        @include respond-to(sm) {
            display: block;
            padding: 10px 15px;
        }

        .bb-select {

            &::before {
                right: 10px;
            }
        }
    } //.catalog-filter__content

    &__range {
        width: 100%;
        padding: 0 10px;
    } //.catalog-filter__range

    &__slider {
        margin-bottom: 15px;
    } //.catalog-filter__slider

    &__category {
        width: 100%;
    } //.catalog-filter__category

    &__btn {
        width: 50%;
        height: 40px;
        line-height: 40px;
        text-transform: uppercase;
        font-family: $f-light;
        font-size: 14px;
        border: none;
        background-color: transparent;
        outline: none;

        &_reset {
            display: block;
            text-align: center;
            border-top: 1px solid $color-border--lightest;
        } //.catalog-filter__btn_close

        &_apply {
            color: #fff;
            border-top: 1px solid $color-main;
            background-color: $color-main;
        } //.catalog-filter__btn_apply

        &_close {
            fill: $color-text;
            background-color: transparent;
        } //.catalog-filter__btn_close

        &_filter {
            display: none;
            height: 60px;
            width: 60px;
            border: 0;
            border-radius: 50%;
            background-color: $color-main;
            box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.16),
                0 2px 10px 0 rgba(0, 0, 0, 0.12);
            position: fixed;
            right: 10px;
            bottom: 10px;
            z-index: 90;
            @include respond-to(sm) {
                display: flex;
            }

            &:focus {
                outline: none;
                border: none;
            }

            .icon {
                margin: auto;
                width: 23px;
                height: 23px;
                fill: #fff;
            }
        } //.catalog-filter__btn_filter
    } //.catalog-filter__btn

    .category {

        &__dropdown {
            margin-top: 10px;
        }
    }
} //.catalog-filter

.catalog-content {
    flex: 1;
    margin-left: auto;
    position: relative;
    @include respond-to(xs) {
        width: 100%;
    }

    &__top {
        min-height: 48px;
        padding: 5px 20px;
        margin-bottom: 15px;
        @include flexCenter;
        @extend %box;
        @include tr;
        @include respond-to(xs) {
            padding: 5px 10px;
        }

        &.no-border {
            border-radius: 0 0 $border-radius $border-radius;
        }
    } //.catalog-content__top

    &__result {
        font-size: 14px;

        strong {
            font-size: 15px;
            color: #000;
        }
    } //.catalog-content__result

    &__sorting {
        margin-left: auto;
        margin-right: 30px;
        @include respond-to(xs) {
            margin-left: 0;
            margin-right: 15px;
        }

        .bb-select {

            &::before {
                right: 10px;
            }
        }

        .selectize-control.single .selectize-input {
            border-color: $color-border--lighten;
        }

        .selectize-dropdown.single {
            border-color: $color-border--lighten;
        }
    } //.catalog-content__sorting

    &__view {
        @include max-screen(425) {
            margin-left: auto;
        }
    } //.catalog-content__view

    &__map {
        display: none;
        position: relative;

        #catalog-map {
            width: 100%;
            height: 575px;
            margin-bottom: 30px;
            @include respond-to(xs) {
                height: 425px;
            }
            @include max-screen(375) {
                height: 375px;
            }
            @include respond-to(i5) {
                height: 320px;
            }
        }
    } //.catalog-content__map

    &__empty {
        width: 100%;
        padding: 30px 0;
    } //.catalog-content__empty
} //.catalog-content

.empty-search {
    @include flexCenter;
    justify-content: center;
    text-align: center;

    &__title {
        font-family: $f-bold;
        font-weight: 600;
        font-size: 20px;
    } //.empty-search__title

    &__text {
        display: block;
        margin-bottom: 30px;
        font-family: $f-light;
    } //.empty-search__text
} //.empty-search

.metro-checked {

    &__list {
        margin-bottom: 10px;
    } //.metro-checked__list

    &__item {
        margin-right: 3px;
        font-size: 13px;
        @extend %textLight;
        color: $gray--light;
        position: relative;
    } //.metro-checked__item

    &__icon {
        cursor: pointer;
        position: relative;
        top: -5px;
        @include tr;

        &:hover {
            color: $gray--darkest;
        }
        @include respond-to(xs) {
            font-size: 17px;
        }
    }
} //.metro-checked
